import { mapState, mapActions } from 'vuex'
import { getDiacrisisResult, getCheckCardByNumber } from '@/utils/ApiList.js'
const app = getApp()
export default {
    data() {
        return {
            bg:this.imgs.loginBg,            
            statusContext:'',
            bindStatus: '未知',
			cardId: app.globalData.cardId
        }
    },
    computed: {
        ...mapState([
            'ispName'
        ]),
        ...mapState('flowData',[
            'bgTop',
            'flowTopBg',
        ]),
    },
    mounted () {
        if (this.cardId!='') {
			uni.showLoading({
				title:'加载中'
			})
            getDiacrisisResult({card_no:this.cardId}).then(res => {
                console.log(res)
                this.bindStatus = res.data.card_status
				uni.hideLoading()
            }).catch(err => {
                this.bindStatus='未知'
				uni.hideLoading()
            })
        }        
    },
    methods: {        
		
    },
    components: {
        
    }
}
